.overlay {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }
  
  .drawer {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 420px;
    height: 100%;
    right: 0;
    background: #fff;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.2);
    padding: 30px;
  
    .items{
      overflow: auto;
      margin-bottom: 40px;
    }
  
    h2 {
      margin: 0px;
    }
  }
  .items {
    flex: 1;
  }
  .cartTotalBlock {
    ul {
      margin-bottom: 40px;
      li {
        display: flex;
        align-items: flex-end;
        margin-bottom: 20px;
  
        div {
          flex: 1;
          height: 1px;
          border-bottom: 1px dashed #000; //dfdfdf
          position: relative;
          top: -4px;
          margin: 0 7px;
        }
      }
    }
    .greenButton{
      position: relative;
  
      &:hover{
        img{
          transform: translateX(5px);
        }
      }
  
      img{
        position: absolute;
        right: 30px;
        top: 20px;
        transition: transform 0.2s ease-in-out;
      }
    }
  }
  
  .greenButton {
    width: 100%;
    height: 55px;
    background: #9dd558;
    border-radius: 18px;
    border: 0;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.2s ease-in-out;
  
    &:hover {
      background: lighten(#9dd558, 5%);
    }
  
    &:active {
      background: darken(#9dd558, 5%);
    }
  
  }
  
  .cartItem {
    border: 1px solid #f3f3f3;
    border-radius: 20px;
    overflow: hidden;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  
    p {
      font-size: 16px;
    }
  
    b {
      font-size: 14px;
    }
    .removeBtn {
      opacity: 0.5;
      transition: opacity 0.2s ease-in-out;
  
      &:hover {
        opacity: 1;
        cursor: pointer;
      }
    }
  }
  